import { DropzoneOptions } from "dropzone";
import { getUrlParam } from "./flm_iframe";
import { loadFolders } from "./loadFolders";
import { IFolderStore } from "./IFolderStore";

export const dropZoneOptions = ($options): DropzoneOptions => {
    return {
        paramName: "doc[]", // The name that will be used to transfer the file
        uploadMultiple: false,
        parallelUploads: 5,
        timeout: 0,
        clickable: '#upload-button',
        dictDefaultMessage: 'Arrástrelos acá',

        init: function () {
            var _this = this; // For the closure
            this.on('success', function (file,
                response) {
                if (typeof response === 'string' && response.endsWith('OK')) {
                    loadFolders($options.id_negocio);
                    (Alpine.store('folder') as IFolderStore)
                        .debouncedLoadItems();
                    return $options.notifyUpload(file)

                }
                //@ts-ignore
                this.defaultOptions.error(
                    file, JSON.stringify(response)
                );

            });
        },
        headers: {
            'Authorization': 'Bearer ' + getUrlParam(
                'token')
        },
        //   acceptedFiles: "{{ implode(',', $helper->availableMimeTypes()) }}",

    }
}